import { Component, OnInit } from '@angular/core';
import { HttpService } from '../http.service';
import { Contact} from '../contact';
import {FormsModule } from '@angular/forms';
@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.css']
})
export class ContactComponent implements OnInit {
  fname : String ="";
  lname : String ="";
  phone : String ="";
  email : String ="";
  uid : String = "" ;

  contactlist : Contact[] = [];
  
  constructor(public serobj : HttpService) { }

  ngOnInit() {
      this.getContacts();
  }
getContacts(){
  this.serobj.getData().subscribe((res)=>{
    this.contactlist = res;
  },
  (err)=>{
    console.log(err);
  }
  )
}

addContact(data){
  this.serobj.add(data).subscribe((res)=>{
    this.getContacts();
  },
  (err)=>{
    alert(err);
  }
  )
}

del(data){
  console.log(data)
  this.serobj.delete(data._id).subscribe((res)=>{
      this.getContacts();
  })
}

edit(value2){ 
  this.uid =  value2._id;
 this.fname  = value2.first_name;
 this.lname  = value2.last_name;
 this.phone   =  value2.phone;
 this.email = value2.email;
}

updates(value){
  // console.log(value);
  let editData={
    _id: this.uid,
    fname : value.fname,
    lname : value.lname,
    phone : value.phone,
    email : value.email
  }
  // console.log(editData);
    this.serobj.update(editData).subscribe((res)=>{
      this.getContacts();
    })
}

}
